	module.exports = {"data":{"page":{"id":"/Users/dcs619/Dropbox/davidstevens/content/pages/2--about/index.md absPath of file >>> MarkdownRemark","html":"<p>I’m a professional developer, trainer, listener, and problem solver.  I work remotely in <a href=\"http://lifeingreenville.com\">Greenville, SC</a> as a senior developer and consultant for <a href=\"https://kingdomfirstsolutions.com\">Kingdom First</a>.  I get excited about coffee, sushi, card games, spreadsheets, and the outdoors, but not always in that order.</p>\n<p>Want to connect, chat, or ask a question?  My <a href=\"/contact\">DM’s</a> are always open.</p>\n<h3>My Background</h3>\n<p>I was a senior web developer for over 5 years at NewSpring Church, who had 35k weekly attendees, 180k+ user profiles, and 19 locations across South Carolina.  I led the transition to and development of Rock RMS as a single source of truth to automate internal processes, retool the user portal, and leverage native apps.</p>\n<p>Before that, I worked as a software contractor for law enforcement, as a network analyst for a law firm, and a few other roles where I tried to take something apart, break it, and get it working again.</p>\n<h3>My Biography</h3>\n<p>David is a web developer with over a decade’s worth of mistakes and problem solving to show for it.  He loves finding ways to be more efficient, doing things with intensity, and sleeping outdoors.  You can find him on Twitter and his website, <a href=\"https://davidstevens.us\">davidstevens.us</a>, except when he’s spending time with his wife and daughter.</p>","frontmatter":{"title":"About"}},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/about/"}}

